// 格式化时间
export function formatDate(date, fmt) {
  // 1.获取年份
  // y+ 1个或者多个y  yyyy:2021
  // y* 0个或者多个y
  // y? 0个或者1个y
  if (/(y+)/.test(fmt)) {
    // RegExp.$1 指的是与正则表达式匹配的第一个子匹配
    fmt = fmt.replace(RegExp.$1, (date.getFullYear() + '').substr(4 - RegExp.$1.length));
  }

  // 2.获取月日等
  let o = {
    'M+': date.getMonth() + 1,
    'd+': date.getDate(),
    'h+': date.getHours(),
    'm+': date.getMinutes(),
    's+': date.getSeconds(),
  };
  for (let k in o) {
    if (new RegExp(`(${k})`).test(fmt)) {
      let str = o[k] + '';
      fmt = fmt.replace(RegExp.$1, RegExp.$1.length === 1 ? str : padLeftZero(str));
    }
  }
  return fmt;
}

// 不足两位补足两位 04:05:09
function padLeftZero(str) {
  // str=4 -> 004 截取 1 位 -> 04
  return ('00' + str).substr(str.length);
}

// 处理大于1w的数字
export function handleNum(num) {
  if (num > 10000) {
    num = (num / 10000).toFixed(1);
    return num + '万';
  } else {
    return num;
  }
}

// 处理音乐时长的时间
export function handleMusicTime(time) {
  // 如果超过了100000 基本都是毫秒为单位的了 先转成秒的
  time = parseInt(time);
  if (time > 10000) {
    time = Math.floor(time / 1000);
  } else {
    time = Math.floor(time);
  }
  let m = Math.floor(time / 60);
  let s = Math.floor(time % 60);
  m = m < 10 ? '0' + m : m;
  s = s < 10 ? '0' + s : s;
  return m + ':' + s;
}

// 将播放时长还原为秒数
export function returnSecond(time) {
  time = time.split(':');
  let m = parseInt(time[0]);
  let s = parseInt(time[1]);
  return m * 60 + s;
}

// 生成当前时间戳
export function getTimeStamp() {
  return Date.now();
}

//获取登录状态
export async function checkLogin(){
  let re = await this.$request("/login/status");
  //检查登录状态
  console.log(re);
  return re.data.data.profile;//返回用户账号信息，如果没有登录则这个值中profile属性为undefined
}

//更新用户信息
export async function updateUserInfo(val){
  this.$store.state.userInfo = val;
  
  localStorage.setItem("userInfo",JSON.stringify(val));
  this.$store.state.isLogin = val?true:false; 
}

export function deepClone(o){
  //判断o是对象还是数组
  if(Array.isArray(o)){//要优先判断是不是数组！！！因为数组用typeof判断结果还是object
    var result = [];
    for (var i = 0; i<o.length; i++){
      result.push(deepClone(o[i]));//递归，层层拆解对象
    }
  }else if(typeof o == 'object'){
    //对象
    var result = {};
    for (var k in o){
      result[k] = deepClone(o[k]);
    }
  }else{
    //基本类型值
    var result = o;
  }
  return result;
}


